import './ProfilePage.styl';

import PropTypes from 'prop-types';

import {
  Header,
  HeaderBottom,
  BodyProfile
} from 'components';

import React, {
  Component,
} from 'react';

import { bindActionCreators }       from 'redux';
import { connect }                  from 'react-redux';

import {
  UserActions
} from 'actions';

@connect(state => ({
  userData: state.userReducer
}), dispatch => ({
  userActions: bindActionCreators(UserActions, dispatch)
}))

export default class ProfilePage extends Component {
  static propTypes = {
    userData: PropTypes.object,
    userActions: PropTypes.object
  };

  render() {
    const {
      userData
    } = this.props.userData;

    const {
      userActions
    } = this.props;

    return (
      <div className="c-profile-page">
        <Header
          indexPage={4}
        />
        <HeaderBottom
          bigTitle="Profile"
          oneBlock={true}
        />
        <div className="body-profile-wrapper">
          <BodyProfile
            data={userData}
            actions={userActions}
          />
        </div>
      </div>
    );
  }
}



